import React, { useState } from "react"
import { Tabs, Tab } from "react-bootstrap"
import IntestazioneHelpPanel from "../../../panels/HelpSidePanel/components/IntestazioneHelpPanel"
import "./ProcessDoubtingMainHelp.css"
import { Link } from "react-router-dom"
import { ReactComponent as Legend01 } from "./icons/dubitare_disposizione.svg"
import Legend02 from "./icons/dubitare_disposizioneGIF.gif"
import BadgeLegenda from "../../../panels/HelpSidePanel/components/BadgeLegenda"
import { useTranslation, Trans } from "react-i18next"

export default function ProcessDoubtingMainHelp() {
  const [key, setKey] = useState("info")
  const { t } = useTranslation(["translation", "doubting"])
  return (
    <>
      <IntestazioneHelpPanel
        tappa={2}
        linkTappa1={"/doubt/phase1"}
        linkTappa2={"/doubt/phase2"}
        linkTappa3={"/doubt/phase3"}
        linkItinerario={"/itineraries#doubt"}
        titolo={t('dubitare')}
        linkApprofondimento="/doubt/phase2/focus"
        nomeItinerario={t("doubting:help_panel.itinerario_dubbio")}
      />
      <Tabs
        className="mt-2"
        id="info-tabs"
        activeKey={key}
        onSelect={(k) => setKey(k)}
      >
        <Tab
          tabClassName="tab-info"
          eventKey="info"
          title={t("help_panel.spiegazione")}
        >
          <p className="mt-3">
            <strong>{t("help_panel.di_cosa_si_tratta")}</strong>
          </p>
          <Trans i18nKey={"help_panel.di_cosa_si_tratta"} t={t} ns="doubting">
            <p>
              Si definisce{" "}
              <span style={{ color: "#BBBBFF" }}>testo dubitativo</span> un tipo
              di testo che torna continuamente sui propri passi per cancellare e
              riscrivere quanto affermato in precedenza. Il lettore si trova di
              fronte a un testo instabile, allinterno del quale entrano in
              competizione e si sommano numerose versioni possibili della storia
              che il narratore cerca di raccontare con molti dubbi e difficolt.
              La visualizzazione mostra la presenza del testo dubitativo
              allinterno dellopera e permette di indagare il modo in cui tale
              tipo di testo altera la linearit del meccanismo narrativo.
            </p>
          </Trans>
          <p>
            <strong>{t("help_panel.come_funziona")}</strong>
          </p>
          <Trans i18nKey={"help_panel.come_funziona"} t={t} ns="doubting">
            <p>
              La visualizzazione pu essere esplorata in due modi:
              <br /> Il primo modo permette di avere un colpo docchio
              sullestensione del processo in tutta lopera. Le colonne, che
              rappresentano i testi, sono disposte cronologicamente per data di
              prima pubblicazione lungo lasse orizzontale e colorate secondo la
              presenza di quattro tipi di testo:
              <span style={{ color: "#BBBBFF" }}>testo dubitativo</span> (zona
              di espressione del dubbio);{" "}
              <span style={{ color: "#FFD337" }}>testo oggetto di dubbio</span>{" "}
              (zona del testo messa in dubbio); testo dubitativo e oggetto di
              dubbio (zona del testo in cui viene espresso un dubbio e di cui
              poi si dubita); testo non dubitativo (zona neutra).
              <br />
              Ogni colonna se selezionata si apre e mostra, tramite una scala
              cromatica dintensit crescente, la quantit di caratteri che 
              stata messa in discussione una o pi volte dal processo
              dubitativo; linformazione  riportata in percentuale accanto al
              tipo di testo corrispondente. Il numero di volte che un testo
              dubitativo o un testo oggetto di dubbio sono stati messi in
              discussione determina il
              <i>Numero di livelli</i>. Varie funzioni permettono di esplorare i
              dati raccolti, filtrando i testi secondo diversi criteri (
              <i>
                Cerca per; Tipo di pubblicazione; Numero di livelli, Filtro
                cronologico
              </i>
              ) o modificando la loro scala di valore (<i>Lunghezza</i>).
              Inoltre  possibile riorganizzare la successione delle barre
              scegliendo un tipo di testo come parametro ordinante. Il secondo
              modo mostra landamento del processo dubitativo nel testo
              selezionato. In questo caso  possibile vedere le singole
              occorrenze dei diversi tipi di testo, ma soprattutto in che modo
              si creano i <i>livelli</i>.
            </p>
          </Trans>
          <p>
            <strong>{t("help_panel.qualche_pista_di_lettura")}</strong>
          </p>
          <Trans
            i18nKey={"help_panel.qualche_pista_di_lettura"}
            t={t}
            ns="doubting"
          >
            <p>
              Il primo aspetto che emerge dalla visualizzazione  la crescita
              esponenziale del fenomeno a partire dalla fine degli anni
              Cinquanta, ma non solo. Nello stesso periodo in cui si intensifica
              la presenza del{" "}
              <span style={{ color: "#BBBBFF" }}>testo dubitativo</span> anche
              la presenza di{" "}
              <span style={{ color: "#00C19C" }}>
                testo dubitativo e oggetto di dubbio
              </span>{" "}
              aumenta, suggerendoci che pi il testo dubita di se stesso minando
              il proprio statuto, pi il{" "}
              <span style={{ color: "#BBBBFF" }}>testo dubitativo</span> a sua
              volta diventa fragile e oggetto di continue negazioni e
              riformulazioni. Passando a esaminare la struttura interna dei
              singoli racconti o romanzi, la visualizzazione mostra la diversa
              configurazione del processo dubitativo a seconda del testo. In
              particolare, rispetto al modo in cui si strutturano i livelli, si
              possono individuare due tendenze: a) da un lato troviamo opere che
              si costruiscono su catene di ipotesi frammentate e di estensione
              contenuta, stabilendo cos un percorso di lettura fortemente
              instabile (es. <i>Ti con Zero</i>); b) dallaltro invece opere
              che, dopo aver accumulato alcune occorrenze di{" "}
              <span style={{ color: "#BBBBFF" }}>testo dubitativo</span>,
              tendono verso la fine ad annullare il percorso compiuto,
              includendo nellultimo{" "}
              <span style={{ color: "#FFD337" }}>oggetto di dubbio</span> una
              grande quantit di testo (es. <i>Linseguimento</i>). Nel primo
              caso il numero dei livelli sale molto di pi che nel secondo. Le
              due tendenze sono pi evidenti nelle forme brevi, ma anche nei
              romanzi si possono individuare strategie simili nella
              distribuzione delle occorrenze del processo dubitativo (vedi{" "}
              <Link to="/doubt/phase2/focus">Approfondimento</Link>
              ).
            </p>
          </Trans>
        </Tab>
        <Tab tabClassName="tab-info" eventKey="legenda" title={t('help_panel.legenda')}>
          <div className="mt-2">
            <div>
              <small>
                {t("doubting:help_panel.disposizione_sulle_colonne")}
              </small>
            </div>
            <div className="row">
              <div className="col-md-6">
                <Legend01 width="124" />
              </div>
              <div className="col-md-6 text-left">
                <img width="157" src={Legend02} alt="Legenda" />
                <div>
                  <Trans
                    i18nKey={"help_panel.testo_gif"}
                    t={t}
                    ns="doubting"
                  >
                    <small className="badge-legenda-small">
                      Testi disposti secondo data di prima pubblicazione. Apri
                      la colonna cliccando, poi scorri verso il basso per vedere
                      landamento del processo dubitativo.
                    </small>
                  </Trans>
                </div>
              </div>
            </div>
          </div>
          <div className="row mt-2">
            <div className="col-md-12">
              <div>
                <small>{t("doubting:help_panel.tipo_di_fenomeno")}</small>
              </div>
              <div>
                <BadgeLegenda color="#BBBBFF" name={t("doubting:help_panel.testo_dubitativo_dt")} />
                <BadgeLegenda color="#FFD337" name={t("doubting:help_panel.oggetto_di_dubbio")} />
                <BadgeLegenda
                  color="#00C19C"
                  name={t("doubting:help_panel.dubitativo_e_oggetto_di_dubbio")}
                />
                <BadgeLegenda color="#C6CACF" name={t("doubting:help_panel.non_dubitativo")} />
              </div>
            </div>
          </div>
        </Tab>
      </Tabs>
    </>
  )
}
